html,body{
  width: 100%;
  height: 963px;
  direction: @dir;
}
@media(max-width: 560px){
  html,body{
    height: 545px;
  }
}
body{
  padding: 0;
  margin: 0;
  position: relative;
  overflow-x: hidden;
  background: #000 url('/img/psy/bg.jpg') no-repeat;
  background-size: cover;
  background-position: 50%;
}
body{
  font-family: Arial;
}
body > div, body > h1, body > h2, body > h3, body > img, body > .wrap{
  display: block;
  margin: 0 auto;
  text-align: center;
}

h1,h2,h3{
  color: #fff;
  font-weight: 100;
}
h1{
  font-size: 35.7px;
  margin-top: 54px;
  margin-bottom: 17px;
}
h1 span{
  color: #f2b31d;
}
h2{
  font-size: 34px;
  font-weight: bold;
  margin-bottom: 28px;
}
h2 span{
  font-size: 56px;
  color: #009aff;
  font-weight: bold;
}
h3{
  font-size: 16px;
  margin-bottom: 39px;
}
@media(max-width: 560px){
  h1{
    font-size: 22.7px;
    margin-top: 54px;
    margin-bottom: 17px;
  }
  h2{
    font-size: 17px;
    margin-bottom: 28px;
  }
  h2 span{
    font-size: 33px;
    color: #009aff;
  }
}
button.submit{
  position: relative;
  display: block;
  height: 144px;
  max-width: 549px;
  width: 100%;
  margin: -13px auto 0;
  border: 0;
  outline: none;
  cursor: pointer;
  color: transparent;
  padding: 0;
  background: url('/img/psy/Button@{path}.png') no-repeat;
  background-size: cover;
  background-position: 50% 1%;
  &:hover{
    background-position: 49% 96%;
  }
  span{
    position: relative;
    display: block;
    max-width: 67%;
    margin: 0 auto;
    .margin-left(9%);
    top: 0px;
    font-size: 39px;
    font-weight: 700;
    color: #03131c;
    font-style: oblique;
    transform:scale(1,1.8);
    -webkit-transform:scale(1,1.8);
    -moz-transform:scale(1,1.8);
    -ms-transform:scale(1,1.8);
    -o-transform:scale(1,1.8);
    text-shadow: 1px 0.6px 0 #d2d516;
  }
}
/** EOS Languages **/
@media(max-width: 560px){
  button.submit{
    max-width: 218px;
    max-height: 60px;
  }
  button.submit span{
    top: -1px;
    font-size: 12px;
  }
}
/** EOF Languages **/
.cer{
  margin-top: 20px;
}
img{
  max-width: 100%;
}
.ppl{
  background: url('/img/psy/ppl.png') no-repeat;
  background-size: contain;
  height: 403px;
  max-width: 691px;
  width: 100%;
  margin: 0 auto;
}
.ppl > video{
  position: relative;
  height: 89%;
  width: 101%;
  top: 5.5%;
  left: -0.3%;
}
.ppl.second > video{
  position: relative;
  height: 100%;
  width: 100%;
  top: 1.5%;
  left: 0%;
  max-height: 382px;
}
@media(max-width: 715px){
  .ppl{
    max-width: 560px;
    max-height: 330px;
  }
  .ppl > video{
    height: 90.5%;
    width: 93.4%;
    top: 5.3%;
    left: 0.3%;
  }
  .ppl.second > video {
    position: relative;
    height: 100%;
    width: 100%;
    top: -2.5%;
    left: 0.2%;
    max-height: 370px;
    max-width: 527px;
  }
}
@media(max-width: 560px){
  h1,h2,h3{
    padding: 0 5px;
  }
  .ppl{
    max-width: 310px;
    max-height: 191px;
  }
  .ppl > video{
    height: 87.5%;
    width: 93.4%;
    top: 4.7%;
  }
  .ppl.second > video {
    position: relative;
    height: 100%;
    width: 100%;
    top: -7%;
    left: 0.2%;
    max-height: 370px;
    max-width: 291px;
  }
  .cer{
    max-width: 161px;
  }
}


.container{
  width: 100%;
  max-width: 673px;
}
script{
  display: none;
}
.loading {
  color: #fff;
  position: relative;
  top: -93px;
  width: 100%;
  z-index: 99;
}
.loading .bg {
  position: absolute;
  top: -29px;
  left: 0;
  width: 100%;
  height: 111px;
  background: #000;
  display: block;
  z-index: -1;
}
div.loading{
  background-color: transparent;
}
.wrap{
  width: 100%;
  max-width: 672px;
}

/** form **/
input#submit{
  text-transform: uppercase;
  .gradientHover(top,to bottom,#ffab00,#fff500,0%,64%);
}

